import React, { Component } from 'react'
import bridge from '@vkontakte/vk-bridge'
import { Group, ScreenSpinner, List, RichCell, Link, Separator, Placeholder, ActionSheet, ActionSheetItem, Alert } from '@vkontakte/vkui'
import Icon28EditOutline from '@vkontakte/icons/dist/28/edit_outline';
import Icon28DeleteOutlineAndroid from '@vkontakte/icons/dist/28/delete_outline_android';


import SnackbarError from './SnackbarError';

export class MyGroups extends Component {
  state = {
    data: null,
    snackbar:null,
  }
  componentDidMount() {
    this.props.setPopout(<ScreenSpinner />)
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/get-tags", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "body": JSON.stringify({
        user_id: String(this.props.state.user.id)
      }),
      "method": "POST",
    })
    .then(response => response.json())
    .then(data => {
      
      this.setState({ data: data.tags })
      this.props.setPopout(null)
    }).catch(err => {
      this.props.setPopout(null)
      this.setState({snackbar:<SnackbarError close={() => this.setState({snackbar:null})}/>, data:'error'})
    })
  }

  openMenu = (item, i) => {
    this.props.setPopout(
      <ActionSheet onClose={() => this.props.setPopout(null)}>
        <ActionSheetItem autoclose before={<Icon28EditOutline />} onClick={() => this.edit_(item.body, item.id)}>
          Редактировать
        </ActionSheetItem>
        <ActionSheetItem mode="destructive" before={<Icon28DeleteOutlineAndroid />} onClick={() => this.openConfirm(item.id, i)}>
          Удалить  
        </ActionSheetItem>
      </ActionSheet>
    )
  }

  openConfirm = (id, i) => {
    this.props.setPopout(
      <Alert
      onClose={() => this.props.setPopout(null)}
      actions={[{
        title: 'Нет',
        autoclose: true,
        mode: 'cancel'
      }, {
        title: 'Да',
        autoclose: true,
        mode: 'destructive',
        action: () => this.delete(id, i),
      }]}>
       Вы уверены?
      </Alert>
    )
  }

  delete = (id, i) => {
    console.log(i)
    this.props.setPopout(<ScreenSpinner />);
    fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/remove-tag", {
      "headers": {
        "accept": "*/*",
        "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
        "content-type": "application/json;charset=utf-8",
      },
      "referrer": "https://appvk.flights.ru/",
      "referrerPolicy": "no-referrer-when-downgrade",
      "body": JSON.stringify({tag_id:id, user_id:this.props.state.user.id}),
      "method": "POST",
    })
    .then(response => response.json())
    .then(check => {
      if(check.response === 'ok'){
        this.state.data.splice(i, 1);
        this.props.setPopout(null);
      }
    })
  }

  edit_ = (string, id) => {
    let name = string.split('#')[1];
    let screenName = string.split('@')[1];
    const { state } = this.props
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppAllowMessagesFromGroup", {"group_id": data.response[0].id, "key": "dBuBKe1kFcdemzB"})
          .then(() => {
            bridge.sendPromise("VKWebAppCallAPIMethod", {
              method: "wall.get",
              request_id: 2,
              params: {
                domain: screenName,
                access_token: auth.access_token,
                v: 5.107,
              }
            })
            .then(async tags => {
              let tagsIn = [];
              let tagsOut = [];
              let itemsWall = tags.response.items;
              let tags_obj = this.state.data.filter(element => { if(element.body.split('@')[1] ===  screenName) { return true; }});
              let tags_name = tags_obj.map(e => { return e.body});
              console.log(tags_name);
              itemsWall.forEach((group) => {
                let match = [...group.text.matchAll(/(#.*@.*)/g)];
                if (match.length) {
                  if (match.length > 2) {
                    let matchOut = match[2];
                    tagsOut.push(matchOut)
                }
                let matchIn = match[0];
                tagsIn.push(matchIn)
                }
              });
              tagsIn = tagsIn.map((item) => 
                item[0]
              )
              tagsIn = tagsIn.filter((v,i) => tagsIn.indexOf(v) === i)
              tagsOut = tagsOut.map((item) => 
                item[0]
              )
              tagsOut = tagsOut.filter((v,i) => tagsOut.indexOf(v) === i)
              let obj = {
                name:name,
                id:id,
                groupId:data.response[0].id, 
                from:tagsOut.map((item) => {
                  return {
                    tag: item,
                    checked: tags_name.indexOf(item) !== -1
                  }
                }),
                to:tagsIn.map((item) => {
                  return {
                    tag: item,
                    checked: tags_name.indexOf(item) !== -1
                  }
                })
              }
              this.props.onChangeGroups('obj', obj);
              this.props.openModal('subscribeCities');
              this.props.setPopout(null);

            })
            .catch((err) => {this.props.setPopout(null); console.log(err)})
          })
          .catch((err) => {this.props.setPopout(null); console.log(err)})
        })
        .catch((err) => {this.props.setPopout(null); console.log(err)})
      }
    })
    .catch(() => this.props.setPopout(null))
  }

  render() {
    const {
      data
    } = this.state
    return (
      <Group>
        {this.state.data === 'error' ? <Placeholder>Ошибка интернет-соеденения</Placeholder>:
        data && (
        <div>
          {data.length === 0 ? <Placeholder>У вас нет подписок.</Placeholder>:
        <List>
          {
            
              data.map((item, i) =>
                <div key={i}>
                  <RichCell
                    onClick={() => this.openMenu(item, i)}
                    text={item.group_name}
                    caption={
                      "Куда: "+item.body.split("#")[1].split("@")[0]
                    }
                    after={
                      <div>
                        <Link>
                          <Icon28EditOutline />
                        </Link><br /><br />
                        <Link>
                          <Icon28DeleteOutlineAndroid />
                        </Link>
                      </div>
                    }
                  >
                    {item.category}
                  </RichCell>
                  <Separator />
                </div>
              )
            
          }
        </List>
        }
        </div>
        )
      }
      {this.state.snackbar}
      </Group>
    )
  }
}

export default MyGroups
