import React, { Component } from 'react'
import { Panel, PanelHeader, FixedLayout, Tabs, TabsItem, PanelHeaderButton } from '@vkontakte/vkui'
import MyGroups from '../components/MyGroups'
import MyDB from '../components/MyDB'

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';

export class MySubs extends Component {
  state = {
    activeTab: 'groups',
  }

  render() {
    const {
      activeTab
    } = this.state
    const {
      id,
      state
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader left={<PanelHeaderButton onClick={() => this.props.onChangeGroups('activePanel', (activeTab === 'database' ? 'settings' : 'groups'))}><Icon28AddOutline /></PanelHeaderButton>}>Мои подписки</PanelHeader>
        <FixedLayout vertical="top">
          <Tabs>
            <TabsItem
              selected={activeTab === 'groups'}
              onClick={() => this.setState({ activeTab: 'groups' })}
            >Витрина</TabsItem>
            <TabsItem
              selected={activeTab === 'database'}
              onClick={() => this.setState({ activeTab: 'database' })}
            >Настройки</TabsItem>
          </Tabs>
        </FixedLayout>
        <div style={{ marginTop: '60px' }} />
        {
          activeTab === 'groups' ? 
            <MyGroups
              setPopout={this.props.setPopout}
              onChangeGroups={this.props.onChangeGroups}
              openModal={this.props.openModal}
              state={state}
            />
          :
            <MyDB
              setPopout={this.props.setPopout}
              state={state}
              onChange={this.props.onChange}
              go={this.props.go}
              changeDefaultName={this.props.changeDefaultName}
              changeFunction={this.props.changeFunction}
            />
        }
      </Panel>
    )
  }
}

export default MySubs
